import React from 'react';
import ReactDOM from 'react-dom';

import {NavigationBar} from './nav_bar.js'
import generate_nav_bar_tabs from './nav_bar.js'
import './index.css'

var debug = 1; 

var current_browse_location = ""
var nav_bar_tabs = await generate_nav_bar_tabs(current_browse_location); 

class Root extends React.Component {

    constructor(props) {
        super(props); 
        console.log  ("props.nav_bar_tabs constructor: " + props.nav_bar_tabs); 
        this.state = {
            nav_bar_tabs: props.nav_bar_tabs, 
            nav_bar_tab_select: 0, 
            nav_bar_tab_section_select: 0, 
            nav_bar_tab_section_current_article: 0, 
        }
    }

    set_active_tab = (tab_id) => {
        this.setState({
            nav_bar_tab_select: tab_id
        }); 
        nav_bar_tabs = generate_nav_bar_tabs(current_browse_location);
        if (debug === 1) {
            console.log ("The selected tab id is " + tab_id); 
        }
    }

    set_active_tab_section = (section_id) => {
        this.setState({
            nav_bar_tab_section_select: section_id
        }); 
        nav_bar_tabs = generate_nav_bar_tabs(current_browse_location);
        if (debug === 1) {
            console.log ("The selected tab section id is " + section_id); 
        }
    }

    render() {
        console.log  ("props.nav_bar_tabs render: " + this.state.nav_bar_tabs); 
        return (

    <div>

    <nav id="navigation-bar" >

        <NavigationBar 
            nav_bar_tabs={this.state.nav_bar_tabs} 
            nav_bar_tab_select = {this.state.nav_bar_tab_select}
            set_active_tab = {this.set_active_tab}
        />

    </nav>

    </div>

        )
    }

}

let root = ReactDOM.createRoot(document.getElementById('body-root')); 

root.render(<Root nav_bar_tabs={nav_bar_tabs}/>)
